body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body {
  height: 100%;
}

#root {
  height: 100%;
}

.loginWrapper {
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.loginInner {
  width: 80%;
}

.weui-cells {
  margin-top: 0!important;
  border-left: 6px solid #1AAD19;
}

.weui-cells:before {
  border-top: none!important;
}

.weui-cells:after {
  border-bottom: none!important;
}

.loginBtn {
  margin-top: 60px;
}

.infoWrapper {
  padding-left: 12px;
  padding-top: 12px;
}

.moreWrapper {
  display: flex;
  flex-direction: row;
  margin-top: 20px;
}

.moreItem {
  flex: 1;
  height: 100px;
  padding: 0 20px;
  word-break: break-all;
  font-size: 12px;
  text-align: center;
}

.infoTop {
  text-align: center;
  font-size: 18px;
  font-weight: bold;
  margin-top: 30px;;
}

.moreItem>span {
  font-weight: bold;
}

.amount {
  color: red;
}

